.fram-navleft h5 {
  padding-left: 30px;
  height: 48px;
  line-height: 48px;
  font-size: 14px;
  cursor: pointer;
  color: #000;
}
.fram-navleft h5:hover {
  background-color: #f4f4f4;
}
.fram-navleft .togglable {
  color: #000;
  padding-left: 10px;
  height: 48px;
  line-height: 48px;
  font-size: 16px;
  cursor: pointer;
}
.fram-navleft .togglable:hover {
  background-color: #f4f4f4;
}
.fram-navleft .child-ul li {
  padding-left: 25px;
  height: 48px;
  line-height: 48px;
  font-size: 14px;
  cursor: pointer;
}
.fram-navleft .child-ul li:hover {
  background-color: #f4f4f4;
}
.fram-navleft .active {
  color: #007dff;
}
